<template>
  <div class="wrap">
    <div class="background"></div>
    <div class="top-line"></div>
    <main>
      <search class="search"></search>
      <h2>{{$t('contracts.title')}}</h2>
      <div class="total">
        A Total Of {{total}} verified contract source codes found
      </div>
      <div class="table-wrap">
        <el-table
          :data="contaracts"
        >
          <el-table-column
            align="center"
            :label="$t('contracts.contractAddress')"
            width="360"
          >
            <template slot-scope="scope">
              <router-link :to="'/contractOverview/'+scope.row.contractAddress">
                {{scope.row.contractAddress}}
              </router-link>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            width="360"
            :label="$t('contracts.authorAddress')"
          >
            <template slot-scope="scope">
              <span class="link" @click="_mixin_address_jump(scope.row.onwerAddress)">{{scope.row.onwerAddress}}</span>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            prop="callTimes"
            :label="$t('contracts.callTimes')"
            width="120"
          >
          </el-table-column>
          <el-table-column
            align="center"
            prop="createTime"
            :label="$t('contracts.createTime')"
            :formatter="dataFormate"
            width="220"
          >
          </el-table-column>
          <el-table-column
            align="center"
            :label="$t('contracts.lastTime')"
            width="220"
          >
            <template slot-scope="scope">
              <timeago v-if="scope.row.lastTime !== null" :since="scope.row.lastTime" :locale="getBusLocal" :auto-update="0.5"></timeago>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </main>
    <el-pagination
      small
      class="pagination"
      layout="prev, pager, next, jumper"
      :current-page="page"
      :page-size="size"
      :total="total"
      :pager-count="5"
      @current-change="pageChange">
    </el-pagination>
  </div>
</template>

<script>
  import Search from "../components/search/Search";
  import bus from "../utils/bus";
  import common from "../utils/common";
  import mixin from '../utils/mixin';
  export default {
    mixins: [mixin],
    components: {Search},
    name: "contracts",
    data() {
      return {
        page: 1,
        size: 25,
        total: 0,
        contaracts: []
      };
    },
    created() {
      this.getContractsData();
    },
    methods: {
      getContractsData() {
        let that = this;
        this.$axios.post('/queryContractList', {page: this.page, rows: this.size}).then(function (res) {
          let data = res.data.data;
          that.contaracts = data.rows;
          that.total = data.total;
        })
      },
      pageChange(page) {
        this.page = page;
        this.getContractsData();
      },
      dataFormate(row) {
        let time = new Date(row.createTime);
        return common.format(time,'yyyy-MM-dd hh:mm:ss');
      }
    },
    computed: {
      getBusLocal() {
        return bus.local;
      }
    }
  }
</script>

<style lang="less" scoped>
  .wrap {
    .top-line {
      height: 1px;
    }
    .background {
      width: 100%;
      height: 338px;
      position: absolute;
      top: 0;
      left: 0;
      background: url("../assets/img/home-bg.png");
    }
    main {
      padding: 80px 20px 0;
      position: relative;
      color: white;
      .search {
        margin-bottom: 30px;
      }
      h2 {
        font-size: 36px;
      }
      .total {
        margin: 30px 0;
      }
    }
    .pagination {
      text-align: center;
      margin: 20px auto;
    }
  }
</style>



