<template>
  <div class="wrap">
    <div class="background"></div>
    <div class="top-line"></div>
    <main>
      <search class="search"></search>
      <h2>
        {{$t('address.overview.title')}}
      </h2>
      <section>
        <div>
          <ul>
            <li>
                <span>
                  {{$t('address.overview.name')}}
                </span>
              <span>
                  {{minerInfo.name}}
                </span>
            </li>
            <li>
                <span>
                  {{$t('address.overview.address')}}
                </span>
              <span>
                  {{minerInfo.address}}
                </span>
            </li>
            <li>
                <span>
                  {{$t('address.overview.contracts')}}
                </span>
              <span>
                  {{minerInfo.contracts}}
                </span>
            </li>
          </ul>
        </div>
        <div>
          <ul>
            <li>
                <span>
                  {{$t('miner.overview.transaction')}}
                </span>
              <span>
                  {{minerInfo.transaction}}
                </span>
            </li>
            <li>
                <span>
                  {{$t('miner.overview.rewards')}}
                </span>
              <span>
                  {{minerInfo.rewards}}
                </span>
            </li>
            <li>
              <span>
                  {{$t('address.overview.balances')}}
                </span>
              <span>
                <template v-for="(item, index) in minerInfo.balances">
                    {{item}}<template v-if="index < minerInfo.balances.length - 1">,</template>
                  </template>
                </span>
            </li>
            <li>
                <span>
                  {{$t('address.overview.lockBalance')}}
                </span>
              <span>
                  <template v-for="(item, index) in minerInfo.lockBalances">
                    {{item}}<template v-if="index < minerInfo.lockBalances.length - 1">,</template>
                  </template>
                </span>
            </li>
            <li>
                <span>
                  {{$t('address.overview.paybackBalances')}}
                </span>
              <span>
                  <template v-for="(item, index) in minerInfo.paybackBalances">
                    {{item}}<template v-if="index < minerInfo.paybackBalances.length - 1">,</template>
                  </template>
                </span>
            </li>
          </ul>
        </div>
      </section>
      <aside>
        <header>
          <div>
            <span @click="choiceFlagChange(0)" :class="{'choice':choiceFlag===0}">{{$t('address.myTransactions.title')}}</span>
            <span @click="choiceFlagChange(1)" :class="{'choice':choiceFlag===1}">{{$t('address.myContracts.title')}}</span>
          </div>
          <div class="total">
            A Total Of {{total}} transactions found
          </div>
        </header>
        <div v-if="choiceFlag===0" class="table-wrap">
          <el-table
            :data="transaction"
          >
            <el-table-column
              align="center"
              :label="$t('address.myTransactions.txHash')"
              width="360"
            >
              <template slot-scope="scope">
                <router-link :to="'/transfer_details/'+scope.row.trxId+'/'+scope.row.opType">{{scope.row.trxId}}
                </router-link>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              :label="$t('address.myTransactions.block')"
              width="120">
              <template slot-scope="scope">
                <router-link :to="'/blockDetails/'+scope.row.blockNum">{{scope.row.blockNum}}</router-link>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              :label="$t('address.myTransactions.types')"
              :formatter="getTypeName"
              width="240"
            >
            </el-table-column>
            <el-table-column
              align="center"
              :label="$t('address.myTransactions.age')"
              width="180"
              >
              <template slot-scope="scope">
                <timeago :since="scope.row.trxTime" :locale="getBusLocal"></timeago>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              width="360"
              :label="$t('address.myTransactions.from')"
            >
              <template slot-scope="scope">
                <span class="link" @click="_mixin_address_jump(scope.row.fromAccount)">{{scope.row.fromAccount}}</span>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              width="360"
              :label="$t('address.myTransactions.to')"
            >
              <template slot-scope="scope">
                <span class="link" @click="_mixin_address_jump(scope.row.toAccount)">{{scope.row.toAccount}}</span>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              prop="amountStr"
              :label="$t('miner.myTransactions.value')"
              width="180">
            </el-table-column>
            <el-table-column
              align="center"
              :label="$t('address.myTransactions.fee')"
              width="180">
              <template slot-scope="scope">
                {{scope.row.feeStr}}
                <img v-if="scope.row.guaranteeUse" class="feeShow" src="../assets/img/shouxufei.png"/>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div v-else class="table-wrap">
          <el-table
            :data="contaracts"
            key="contaracts"
          >
            <el-table-column
              align="center"
              :label="$t('address.myContracts.address')"
              width="360"
            >
              <template slot-scope="scope">
                <i class="el-icon-circle-check-outline" v-if="scope.row.status === 2"></i>
                <span class="link" @click="_mixin_address_jump(scope.row.contractAddress)">{{scope.row.contractAddress}}</span>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              prop="callTimes"
              :label="$t('address.myContracts.call')"
              width="200"
            >
            </el-table-column>
            <el-table-column
              align="center"
              :formatter="dateFormate"
              :label="$t('address.myContracts.create')"
              width="220"
            >
            </el-table-column>
            <el-table-column
              align="center"
              show-overflow-tooltip
              :label="$t('address.myContracts.last')"
              width="220"
            >
              <template slot-scope="scope">
                <timeago :since="scope.row.lastTime" :locale="getBusLocal" :auto-update="0.5"></timeago>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <el-pagination
          small
          class="pagination"
          layout="prev, pager, next, jumper"
          :current-page="page"
          :page-size="size"
          :total="total"
          :page-count="5"
          @current-change="pageChange">
        </el-pagination>
      </aside>
    </main>
  </div>
</template>

<script>
  import Search from "../components/search/Search";
  import bus from "../utils/bus";
  import common from "../utils/common";
  import typeObj from "../utils/type"
  import mixin from '../utils/mixin';
  export default {
    mixins: [mixin],
    components: {Search},
    name: "v-address",
    data() {
      return {
        page:1,
        size:10,
        total:0,
        minerName:'',
        address:'',
        choiceFlag: 0,
        minerInfo: {
          name:'',//
          transaction: 0,//
          address: "",//
          rewards: null,//
          contracts: 0//
        },
        transaction: [],
        contaracts: []
      }
    },
    beforeRouteUpdate (to, from, next) {
      if (to.query.address) {
        this.address = to.query.address;
        this.getDataByAddress();
      } else {
        this.minerName = to.query.minerName;
        this.getDataByMinerName();
      }
      next();
    },
    created() {
      if (this.$route.query.address) {
        this.address = this.$route.query.address;
        this.getDataByAddress();
      } else {
        this.minerName = this.$route.query.minerName;
        this.getDataByMinerName();
      }
    },
    methods: {
      pageChange(page) {
        this.page = page;
        if (this.choiceFlag === 0) {
          this.getTransactionData();
        } else {
          this.getContractData();
        }
      },
      getTypeName(row) {
        if (row.opType || row.opType === 0) {
          return typeObj[row.opType];
        } else {
          return typeObj[row.type];
        }
      },
      choiceFlagChange(flag) {
        this.choiceFlag = flag;
        this.page = 1;
      },
      getDataByMinerName() {
        let that = this;
        this.$axios.post('/minerInfo',{name:this.minerName}).then(function (res) {
          let data = res.data;
          that.minerInfo = data.data;
          that.getTransactionData();
        });
      },
      getDataByAddress() {
        let that = this;
        this.$axios.post('/addrStatis',{address:this.address}).then(function (res) {
          let data = res.data;
          that.minerInfo = data.data;
          that.getTransactionData();
        });
      },
      getTransactionData() {
        let that = this;
        this.$axios.post('/queryTrxByAddr',{address:this.minerInfo.address,page:this.page,rows:this.size}).then(function (res) {
          let data = res.data;
          that.transaction = data.data.rows;
          that.total = data.data.total;
        });
      },
      getContractData() {
        let that = this;
        this.$axios.post('/addrContracts',{ownerAddress:this.minerInfo.address,page:this.page,rows:this.size}).then(function (res) {
          let data = res.data;
          that.contaracts = data.data.rows;
          that.total = data.data.total;
        });
      },
      dateFormate(row) {
        return common.format(new Date(row.createTime),'yyyy-MM-dd hh:mm:ss');
      }
    },
    computed: {
      getBusLocal() {
        return bus.local;
      }
    }
  }
</script>

<style lang="less" scoped>
  .wrap {
    .top-line {
      height: 1px;
    }
    .background {
      width: 100%;
      height: 538px;
      position: absolute;
      top: 0;
      left: 0;
      background: url("../assets/img/home-bg.png");
    }
    main {
     padding: 50px 20px;
      position: relative;
      color: white;
      .search {
       margin-bottom: 30px;
      }
      h2 {
        font-size: 36px;
      }
      section {

        div ul li {
          box-sizing: border-box;
          border-bottom: 1px solid rgba(255, 255, 255, .5);
          padding: 12px;
          font-size: 14px;
          span {
            display: inline-block;
            &:first-of-type {
            }
          }
        }
        .left {
          float: left;
        }
        .right {
          float: right;
        }
      }
      aside {
        margin-top: 50px;
        header {
          font-size: 16px;
          padding: 0 15px;
          margin-bottom: 20px;
          div {
            &:first-of-type {
              span {
                display: inline-block;
                margin-right: 20px;
                color: #C6CAD4;
                position: relative;
                cursor: pointer;
                font-size: 14px;
              }
              .choice {
                color: #6042A9;
                &::after {
                  display: block;
                  position: absolute;
                  content: '';
                  width: 22px;
                  height: 2px;
                  background: #6042A9;
                  left: 50%;
                  bottom: -5px;
                  z-index: 1;
                  transform: translateX(-50%);
                }
              }
            }
          }
        }
        .total {
          color: #333333;
          margin-top: 20px;
          font-size: 12px;
        }
        .table-wrap {
          .el-icon-circle-check-outline {
            color: #EB6100;
            position: absolute;
            left: 10px;
            top: 50%;
            transform: translateY(-50%);
          }
        }
        .pagination {
          text-align: center;
          margin-top: 20px;
        }
      }
    }
  }
</style>



