<template>
  <div class="wrap">
    <div class="background"></div>
    <aside>
      <img class="logo" src="../../assets/img/logo.png" @click="logoClick"/>
    </aside>
    <img class="menu-btn" src="../../assets/img/menu-btn.png" @click="MenuShowClose"/>
    <transition name="fade">
      <section v-show="menuShow" class="menu">
        <img class="close" src="../../assets/img/menu-close.png" @click="MenuShowClose"/>
        <ul>
          <router-link to="/">
            <li :class="{'choice':getChoiceIndex === 0}" @click="navChange(0)">
              {{$t('nav.home')}}
            </li>
          </router-link>
          <router-link to="/blocks">
            <li :class="{'choice':getChoiceIndex === 1}" @click="navChange(1)">
              {{$t('nav.blocks')}}
            </li>
          </router-link>
          <router-link to="/transaction">
            <li :class="{'choice':getChoiceIndex === 2}" @click="navChange(2)">
              {{$t('nav.transactions')}}
            </li>
          </router-link>
          <router-link to="/contracts">
            <li :class="{'choice':getChoiceIndex === 3}" @click="navChange(3)">
              {{$t('nav.contracts')}}
            </li>
          </router-link>
        </ul>
      </section>
    </transition>
  </div>
</template>

<script>
  import bus from '../../utils/bus'

  export default {
    name: "v-nav",
    data() {
      return {
        menuShow:false,
        value: true,
        time: '2018-05-25'
      }
    },
    methods: {
      logoClick() {
        this.$router.push({path: '/'});
      },
      changeLang() {
        if (bus.locale === 'en') {
          this.$i18n.locale = 'cn';
          bus.local = 'cn';
        } else {
          this.$i18n.locale = 'en';
          bus.local = 'en';
        }
      },
      MenuShowClose() {
        this.menuShow = !this.menuShow;
      },
      navChange(index) {
        bus.navChoice = index;
      }
    },
    computed: {
      getBusLocal() {
        return bus.local;
      },
      getChoiceIndex() {
        return bus.navChoice;
      }
    }
  }
</script>

<style lang="less" scoped>
  .wrap {
    position: absolute;
    width: 100%;
    box-sizing: border-box;
    height: 39px;
    padding: 0 20px;
    top: 0;
    left: 0;
    z-index: 2;
    a {
      text-decoration: none;
    }
    a:hover, a:visited, a:link, a:active {
      color: inherit;
    }
    .background {
      background-color: rgba(0, 0, 0, 0.15);
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: -1;
      /*z-index: 1;*/
    }
    aside {
      float: left;
      height: 39px;
      line-height: 39px;
      font-size: 0;
      z-index: 100;
      .logo {
        width: 70px;
        height: 26px;
        vertical-align: middle;
        cursor: pointer;
      }
    }
    .menu-btn {
      position: absolute;
      height: 25px;
      right: 20px;
      top: 7px;
    }
    .menu {
      position: fixed;
      right: 0;
      top: 0;
      width: 85px;
      height: 100%;
      background: #0E0A20;
      color: white;
      .close {
        display: block;
        width: 12px;
        margin: 10px auto 50px;
      }
      li {
        font-size: 10px;
        width: 70px;
        height: 18px;
        margin: 0 auto 30px;
        text-align: center;
        line-height: 18px;
      }
      .choice {
        border: 1px solid white;
        border-radius: 8px;
      }
    }
    .fade-enter-active, .fade-leave-active {
      transition: .5s all;
    }
    .fade-enter, .fade-leave-to {
      transform: translateX(100%);
    }
  }
</style>



